___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
}

.header-div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: lightcoral;
    height: 10%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
}

.visible {
    display: block;
}

.content-div {
    position: relative;
    top: 0;
    margin-top: 18%;
    padding: 5%;
    height: 100%;
    background-color: royalblue;
}

#page-1 {
    height: 100%;
}

#page-1 .content-div {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(min-content, 165px));
    grid-template-rows: repeat(auto-fill, minmax(0, 25%));
    gap: 2%;
    justify-content: center;
}

#page-2 {
    height: 100%;
}

#page-2 .content-div {
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: 3rem;
    padding: 10%;
}

#page-2 .content-div p {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 1rem;
    background-color: rgba(255, 255, 255, .5);
}

.card-div {
    display: grid;
    background-color: rgba(255, 255, 255, .4);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    max-height: max-content;
    align-items: start;
    text-align: center;
    padding: 5%;
}

.card-title {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 1px;
    margin-bottom: 1rem;
    min-height: min-content;
}

.card-subject {
    min-height: min-content;
}

.card-content {
    min-height: min-content;
    padding: 2%;
}

.nav-bar-div {
    height: 10%;
    width: 100%;
    background-color: blueviolet;
    position: fixed;
    top: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.page-button {
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    border: none;
    opacity: .7;
    background-color: black;
}

.page-button-not-clicked {
    background-color: white;
}

.hidden {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,4BAA4B;IAC5B,WAAW;IACX,4CAA4C;IAC5C,eAAe;IACf,WAAW;IACX,MAAM;IACN,OAAO;IACP,UAAU;AACd;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,eAAe;IACf,WAAW;IACX,YAAY;IACZ,2BAA2B;AAC/B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,oEAAoE;IACpE,qDAAqD;IACrD,OAAO;IACP,uBAAuB;AAC3B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,SAAS;IACT,YAAY;AAChB;;AAEA;IACI,4CAA4C;IAC5C,aAAa;IACb,yCAAyC;AAC7C;;AAEA;IACI,aAAa;IACb,yCAAyC;IACzC,4CAA4C;IAC5C,uBAAuB;IACvB,kBAAkB;IAClB,kBAAkB;IAClB,WAAW;AACf;;AAEA;IACI,2CAA2C;IAC3C,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;IACvB,WAAW;AACf;;AAEA;IACI,WAAW;IACX,WAAW;IACX,4BAA4B;IAC5B,eAAe;IACf,QAAQ;IACR,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;AACjB","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    height: 100vh;\n}\n\n.header-div {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: lightcoral;\n    height: 10%;\n    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;\n    position: fixed;\n    width: 100%;\n    top: 0;\n    left: 0;\n    z-index: 1;\n}\n\n.visible {\n    display: block;\n}\n\n.content-div {\n    position: relative;\n    top: 0;\n    margin-top: 18%;\n    padding: 5%;\n    height: 100%;\n    background-color: royalblue;\n}\n\n#page-1 {\n    height: 100%;\n}\n\n#page-1 .content-div {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(min-content, 165px));\n    grid-template-rows: repeat(auto-fill, minmax(0, 25%));\n    gap: 2%;\n    justify-content: center;\n}\n\n#page-2 {\n    height: 100%;\n}\n\n#page-2 .content-div {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    gap: 3